}

.title-h1,
.title-h2,
.title-h3,
.title-h4,
.title-h5 {
  font-family: 'intro-bold', sans-serif;
  color: #111110;
}

.title-h1--white,
.title-h2--white,
.title-h3--white,
.title-h4--white,
.title-h5--white {
  color: #fff;
}

.title-h1 {
  font-size: 40px;
  line-height: 1.43;
  font-size: 40px;
  font-size: calc(26px + (40 - 26) * ((100vw - 767px) / (1024 - 767)));
}

@media (max-width: 767px) {
  .title-h1 {
    font-size: 26px;
  }
}

@media (min-width: 1024px) {
  .title-h1 {
    font-size: 40px;
  }
}

@media only screen and (max-width: 767px) {
  .title-h1 {
    line-height: 1.18;
  }
}

.title-h2 {
  font-size: 35px;
  font-size: 35px;
  font-size: calc(24px + (35 - 24) * ((100vw - 767px) / (1024 - 767)));
}

@media (max-width: 767px) {
  .title-h2 {
    font-size: 24px;
  }
}

@media (min-width: 1024px) {
  .title-h2 {
    font-size: 35px;
  }
}

.title-h3 {
  font-size: 30px;
  font-size: 30px;
  font-size: calc(22px + (30 - 22) * ((100vw - 767px) / (1024 - 767)));
}

@media (max-width: 767px) {
  .title-h3 {
    font-size: 22px;
  }
}

@media (min-width: 1024px) {
  .title-h3 {
    font-size: 30px;
  }
}

.title-h4 {
  font-size: 25px;
}

.title-h5 {
  font-size: 20px;
}

body {
  min-width: 320px;
}

.section-title {
  text-align: center;
}

.section-title:after {
  content: "";
  display: block;
  width: 50px;
  height: 1px;
  margin: 15px auto 0;
  background: #FF007C;
}

.button {
  position: relative;
  display: inline-block;
  text-decoration: none;
  background: #FF007C;
  color: #fff;
  padding: 13px 50px;
  line-height: 1.35;
  transition: background 0.2s ease-in;
}

.button:after {
  content: '';
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  box-shadow: 0 2px 5px 0 rgba(255, 0, 124, 0.4);
  opacity: 0;
  transition: opacity 0.2s ease-in;
}

.button:hover, .button:focus {
  background: #ff1a89;
}

.button:hover:after, .button:focus:after {
  opacity: 1;
}

.hero {
