  flex-direction: column;
  margin-bottom: 85px;
  padding: 30px 15px 15px;
  min-height: 685px;
  background-size: cover;
  background-position: 50% 35%;
  min-height: 685px;
  min-height: calc(345px + (685 - 345) * ((100vw - 767px) / (1024 - 767)));
}

@media (max-width: 767px) {
