.card {
  position: relative;
  display: block;
  max-width: 530px;
  text-decoration: none;
  color: #30302F;
  transition: transform 0.2s ease;
  will-change: transform;
}

.card:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: 1px solid rgba(0, 0, 0, 0.2);
}

.card:hover {
  transform: translateY(-5px);
}

@media only screen and (max-width: 767px) {
  .card {
    max-width: 450px;
    margin: 0 auto;
  }
}

.card__inner {
  position: relative;
}

.card__mask {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.card__wave {
  position: absolute;
  top: -50px;
  left: -65px;
}

.card__title {
  position: relative;
  padding: 20px 92px 0 14px;
  color: #fff;
}

@media only screen and (max-width: 767px) {
  .card__title {
    padding-right: 72px;
    padding-top: 15px;
  }
}

.card__subtitle {
  font-size: 16px;
  line-height: 1.35;
}

.card__pic {
  display: block;
  width: 100%;
  height: 330px;
  background-color: #c4c4c4;
  object-fit: cover;
  font-size: 'object-fit: cover';
}

@media only screen and (max-width: 1024px) {
  .card__pic {
    height: 280px;
  }
}

@media only screen and (max-width: 767px) {
  .card__pic {
    height: 250px;
  }
}

.card__price {
  position: absolute;
  left: 0;
  bottom: 0;
  min-width: 134px;
  padding: 18px 20px;
  text-align: center;
  background: #FDD80C;
  font-family: 'proxima-nova-bold', sans-serif;
  font-size: 23px;
  line-height: 1;
}

@media only screen and (max-width: 1024px) {
