@include font(proxima-nova, proxima-nova);
@include font(proxima-nova-semibold, proxima-nova-semibold);
@include font(proxima-nova-bold, proxima-nova-bold);
@include font(proxima-nova-regular-italic, proxima-nova-regular-italic);
@include font(intro-bold, intro-bold);

@mixin ib {
  font-family: 'intro-bold', sans-serif; }

@mixin proxima {
  font-family: 'proxima-nova', sans-serif; }

@mixin proxima-sb {
  font-family: 'proxima-nova-semibold', sans-serif; }

@mixin proxima-b {
  font-family: 'proxima-nova-bold', sans-serif; }

@mixin proxima-it {
  font-family: 'proxima-nova-regular-italic', sans-serif; }


body {
  @include proxima;
  font-size: 18px;
  color: $dark-brown;
  line-height: 1.3;
  @include r(767) {
    font-size: 16px; } }

.title-h1,
.title-h2,
.title-h3,
.title-h4,
.title-h5 {
  @include ib;
  color: $black;
  &--white {
    color: #fff; } }


.title-h1 {
  font-size: 40px;
  line-height: 1.43;
  @include css-lock(26, 40, 767, 1024, font-size);
  @include r(767) {
    line-height: 1.18; } }

.title-h2 {
  font-size: 35px;
  @include css-lock(24, 35, 767, 1024, font-size); }

.title-h3 {
  font-size: 30px;
  @include css-lock(22, 30, 767, 1024, font-size); }

.title-h4 {
  font-size: 25px; }

.title-h5 {
  font-size: 20px; }
